<template>
  <page
    @on-ready="onReady"
    :pullDownRefresh="true"
    :pullUpLoad="true"
    :scrollbar="true"
    @on-refresh="onRefresh"
    @on-load="onLoad"
    ref="page"
    :headerHeightVW="headerHeight / 3.75"
    :style="{ 'z-index': 100 }"
    title=""
  >
    <template slot="header">
      <search v-model="filter.query" placeholder="搜索" @on-search="handleSearch"></search>
    </template>

    <div class="list-container">
      <van-cell
        is-link
        clickable
        class="list-item"
        v-for="(item, index) in list"
        :key="index"
        @click="handleItem(item)"
      >
        <div slot="title" v-html="item.name"></div>
        <div slot="label">当前进度：{{ item.progress }}%</div>
        <!-- <i v-if="item.type == 'Category'" slot="icon" :class="(item.isLeaf ? 'item-icon icon-folder-leaf' : 'item-icon icon-folder')"></i> -->
        <i slot="icon" class="item-icon icon-article"></i>
      </van-cell>
    </div>
  </page>
</template>

<script>
import Page from "../../../components/Page";
import Search from "../../../components/Search";
import List from "./List";
import Detail from "./Detail";
import api from "../../../api";
import watermark from "../../../utils/watermark.js";

export default {
  components: {
    Page,
    Search,
    Detail
  },
  data() {
    return {
      list: [],
      filter: {
        query: "",
        pageNum: 1,
        pageSize: 10
      }
    };
  },
  computed: {
    headerHeight() {
      return 55;
    }
  },
  created() {
    this.$toast.loading({
      loadingType: "spinner",
      duration: 0
    });

    this.onLoad();
  },
  mounted() {},
  methods: {
    onReady() {
      api
        .getMyInfo()
        .then(data => {
          const name = data.name;
          const userid = data.userid;
          watermark.set(`${name} ${userid}`);
        })
        .catch(msg => {
          this.$toast(msg);
        });
    },
    handleSearch(value) {
      this.$refs.page.initScroll();
      this.list = [];
      this.filter = {
        ...this.filter,
        pageNum: 1,
        query: value
      };
      this.$toast.loading({
        loadingType: "spinner",
        duration: 0
      });

      this.onLoad();
    },

    onRefresh() {
      this.filter.pageNum = 1;
      api
        .getList(this.filter)
        .then(data => {
          this.list = data.list;
          this.filter.pageNum++;
          this.$refs.page.finishRefresh();
          this.$toast.clear();
        })
        .catch(msg => {
          this.$toast(msg);
          this.$refs.page.finishRefresh();
        });
    },

    onLoad() {
      api
        .getList(this.filter)
        .then(data => {
          this.list = this.list.concat(data.list);
          this.filter.pageNum++;
          if (data.list.length == 0) {
            this.$refs.page.endLoad();
          } else {
            this.$refs.page.updateLoad();
          }
          this.$toast.clear();
        })
        .catch(msg => {
          this.$toast(msg);
          this.$refs.page.updateLoad();
        });
    },

    handleItem(item) {
      this.$router.push({ name: "detail", params: { id: item.id } });
    }
  }
};
</script>

