import { debug } from 'util';

export const getRect = (el) => {
  if (el instanceof window.SVGElement) {
    const rect = el.getBoundingClientRect()
    return {
      top: rect.top,
      left: rect.left,
      width: rect.width,
      height: rect.height,
    }
  }
  return {
    top: el.offsetTop,
    left: el.offsetLeft,
    width: el.offsetWidth,
    height: el.offsetHeight,
  }
}

export const getQueryStringByName = (name) => {
  const result = window.location.search.match(new RegExp(`[\?\&]${name}=([^\&]+)`, 'i'));

  if (result == null || result.length < 1) {
    return '';
  }

  return result[1];
}

export const loadJs = url => new Promise((resolve, reject) => {
  const script = document.createElement('script')
  script.src = url
  script.type = 'text/javascript'
  document.body.appendChild(script)
  script.onload = () => {
    resolve()
  }
})

export const loadCss = url => new Promise((resolve, reject) => {
  const link = document.createElement('link')
  link.rel = 'stylesheet'
  link.href = url
  document.head.appendChild(link)
  link.onload = () => {
    resolve()
  }
})

export const watermark = ({
  container = document.body,
  width = 120,
  height = 120,
  textAlign = 'left',
  textBaseline = 'middle',
  font = '12px sans-serif',
  fillStyle = 'rgba(0,0,0,0.2)',
  content = '请勿外传',
  rotate = '45',
  zIndex = 1000,
}) => {
  const canvas = document.createElement('canvas')
  canvas.height = height * 2
  canvas.width = width * 2
  canvas.style.width = `${width/3.75}vw`;
  canvas.style.height = `${height/3.75}vw`;
  const ctx = canvas.getContext('2d')
  ctx.scale(2, 2)

  ctx.textAlign = textAlign
  ctx.textBaseline = textBaseline
  ctx.font = font
  ctx.fillStyle = fillStyle
  // ctx.translate(10, 10)
  ctx.rotate(Math.PI / 180 * rotate);
  ctx.fillText(content, 20, 10);

  const base64Url = canvas.toDataURL();
  const watermarkDiv = document.createElement('div');
  watermarkDiv.setAttribute('style', `
    position:absolute;
    margin: 0 20px;
    top:50px;
    left:0;
    width:100%;
    height:100%;
    z-index:${zIndex};
    pointer-events:none;
    background-repeat:repeat;
    background-size: 120px 120px;
    background-image:url('${base64Url}')`);

  container.style.position = 'relative';
  container.insertBefore(watermarkDiv, container.firstChild);
}

export default { getRect, getQueryStringByName }
