<template>
  <page
    ref="detailPage"
    class="detail-page"
    scrollbar
    :headerHeightVW="0/3.75"
    :style="{'z-index': 200}" title="详情">
    <div v-if="detail.id">
      <van-cell-group title=" ">
        <van-cell title="项目名称" :value="detail.name" />
        <van-cell title="计划开始" :value="detail.begin" />
        <van-cell title="计划结束" :value="detail.end" />
        <van-cell title="当前进度" :value="detail.progress + '%'" />
      </van-cell-group>

      <van-cell-group title=" ">
        <van-cell title="项目描述" :label="detail.description" />
      </van-cell-group>

      <van-cell-group title="汇报内容">
        <van-cell v-for="(item, index) in detail.contents" :key="index">
          <div v-html="item.content"></div>
        </van-cell>
      </van-cell-group>
    </div>
  </page>
</template>

<script>
import Page from '../../../components/Page'
import api from '../../../api'

export default {
  components: {
    Page,
  },
  data () {
    return {
      id: '',
      detail: {}
    }
  },
  created () {
    this.id = this.$route.params.id
  },
  mounted () {
    this.loadDetail()
  },
  methods: {

    loadDetail () {
      this.$toast.loading({
        loadingType: 'spinner',
        duration: 0
      })
      api.getDetail({id: this.id}).then(data => {
        this.detail = data
        this.$toast.clear()

      }).catch(msg => {
        this.$toast(msg)
      })
    },
  },

}
</script>

