import request from './request'
import config from './config'

export default {
  getMyInfo: params => request.get(`${config.host}/api/project_manage/user_info`, { params }),
  getList: params => request.get(`${config.host}/api/project_manage/list`, { params }),
  getDetail: params => request.get(`${config.host}/api/project_manage/detail`, { params }),
  sign: params => request.get(`${config.host}/api/sign`, { params }),
  login: params => request.post(`${config.host}/api/login`, params),
}
