<template>
  <div class="amis-search-wrp">
    <van-search class="amis-search" show-action
      shape="round" :placeholder="placeholder" v-model="dataValue">
      <div slot="action" @click="handleSearch">搜索</div>
    </van-search>
  </div>
</template>

<script>
export default {
  props: {
    placeholder: {
      type: String,
      default: '',
    },
    value: {
      type: String,
      default: '',
    },
  },
  data() {
    return {
      dataValue: this.value,
    }
  },
  methods: {
    handleScan() {
      this.$emit('on-scan')
    },
    handleSearch() {
      this.$emit('on-search', this.dataValue)
    },
  },
  watch: {
    dataValue(val) {
      this.$emit('input', val)
    },
    value(val) {
      this.dataValue = val
    },
  },
}
</script>


