import Vue from 'vue';
import Router from 'vue-router';
import List from './page/List.vue'
import Detail from './page/Detail.vue'

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      component: List,
      name: 'list',
      meta: {
        title: '',
      },
      children: [
        {
          path: '/detail/:id',
          component: Detail,
          name: 'detail',
        },
      ],
    },
  ],
})
