import Cookies from 'js-cookie'
import App from './App.vue';
import Bootstrap from '../../utils/bootstrap'
import router from './router'
import config from '../../api/config'
import { loadJs,getQueryStringByName } from '../../utils/tools'
import api from '../../api'


router.beforeEach((to, from, next) => {
  // if (to.name !== 'login') {
  //   const token = Cookies.get('token')
  //   if (!token) {
  //     next('/login')
  //   }

  // }
  if (!to.matched.length) {
    next('/')
  }
  next()
})

if (config.platform === 'qywx') {
  loadJs('http://res.wx.qq.com/open/js/jweixin-1.2.0.js').then(() => {
    api.sign({
      platform: 'qywx',
      url: window.location.href.replace(window.location.hash, ''),
      serial: config.serial,
      agentID: config.agentId,
    }).then((data) => {
      console.log('xxxxx', window.wx)
      window.wx.config({
        beta: true,
        debug: false,
        appId: data.corpId,
        timestamp: data.timeStamp,
        nonceStr: data.nonceStr,
        signature: data.signature,
        jsApiList: ['chooseImage'],
      })
      window.wx.ready(() => {
        if (getQueryStringByName('code')) {
          // api.login({platform: 'qywx', serial: '001', agentID: '1000033', code: getQueryStringByName('code')}).then(data2 => {
          api.login({
            platform: config.platform,
            serial: config.serial,
            agentID: config.agentId,
            code: getQueryStringByName('code'),
          }).then((data) => {
            // Cookies.set('token', data.token)

            Bootstrap.init(App, router)
          })
        } else {
          location.href = `https://open.weixin.qq.com/connect/oauth2/authorize?appid=${data.corpId}&redirect_uri=${window.location.href.replace(window.location.hash, '')}&response_type=code&scope=snsapi_base&state=${config.state}#wechat_redirect`
        }
      })
      window.wx.error((err) => {
        window.alert(err.errMsg)
      })
    }).catch((msg) => {
      window.alert(msg)
    })
  })
} else if (config.platform == 'dd') {
  loadJs('//g.alicdn.com/dingding/dingtalk-jsapi/2.6.0/dingtalk.open.js').then(() => {
    console.log('=====', dd)
    api.sign({
      platform: 'dd',
      url: location.href.replace(location.hash, ''),
    }).then((data) => {
      dd.config({
        agentId: data.agentId,
        corpId: data.corpId,
        timeStamp: data.timeStamp,
        nonceStr: data.nonceStr,
        signature: data.signature,
        jsApiList: [],
      })
      dd.ready(() => {
        dd.runtime.permission.requestAuthCode({
          corpId: data.corpId,
          onSuccess: (info) => {
            console.log('++++', info)
            api.login({ code: info.code, platform: 'dd' }).then((data) => {
              Cookies.set('token', data.token)

              Bootstrap.init(App, router)
            })
          },
        })
      })
      dd.error((err) => {
        alert(err.errorMessage)
      })
    })
  })
} else {
  Bootstrap.init(App, router)
}
